<template>
  <q-page padding class="container">
    <q-card flat bordered class="rounded card-padding-title">
      <q-toolbar class="q-pa-none no-min-height">
        <q-toolbar-title>
          <div class="font-medium text-h5 text-primary-700">{{ title }}</div>
        </q-toolbar-title>
      </q-toolbar>
    </q-card>

    <q-card flat bordered class="rounded q-mt-lg overflow-hidden">
      <q-card-section class="card-pt card-px">
        <div class="row q-col-gutter-md items-center justify-between">
          <div class="col-12 col-md-6 q-pa-none"></div>
          <div class="col-12 col-md-6">
            <div
              class="row q-col-gutter-md items-center text-right justify-end"
            >
              <div class="col-12 col-sm-7 col-md-9">
                <q-input
                  v-model="searchQuery"
                  dense
                  outlined
                  clearable
                  debounce="500"
                  input-class="text-body1 font-medium text-primary"
                  placeholder="Search..."
                  clear-icon="icon-x-close"
                >
                  <template v-slot:prepend>
                    <q-icon
                      name="icon-search-lg"
                      class="hover-text-secondary"
                    />
                  </template>
                </q-input>
              </div>

              <div class="col-auto">
                <q-select
                  dense
                  outlined
                  emit-value
                  map-options
                  color="primary"
                  hide-dropdown-icon
                  :options="filters"
                  :options-dense="false"
                  v-model="activeFilter"
                  display-value="Filters"
                  :option-value="(val, row) => (val ? val.value : null)"
                  class="hover-text-secondary table-select-filter"
                  transition-show="jump-up"
                  transition-hide="jump-down"
                >
                  <template v-slot:prepend>
                    <q-icon
                      name="icon-filter-lines"
                      size="sm"
                      color="primary"
                      class="hover-text-secondary"
                    />
                  </template>
                  <template v-slot:option="scope">
                    <q-item
                      v-bind="scope.itemProps"
                      :disable="scope.opt.required"
                      class="hover-text-secondary"
                      :class="{ ['hover-bg-accent']: !scope.opt.required }"
                    >
                      <q-item-section>
                        <q-item-label
                          :class="
                            scope.opt.required || scope.selected
                              ? 'text-secondary'
                              : ''
                          "
                        >
                          {{ scope.opt.label }}
                        </q-item-label>
                      </q-item-section>
                    </q-item>
                  </template>
                </q-select>
              </div>
            </div>
          </div>
        </div>
      </q-card-section>

      <q-card-section
        class="q-pa-none"
        style="
          max-width: 100%;
          height: 70vh;
          max-height: 70vh;
          min-height: 300px;
        "
      >
        <div class="scroll fit card-px card-pt-sm">
          <BrandsList
            class="card-pb"
            :status="activeFilter"
            :search="searchQuery"
            :reverseList="true"
          />
        </div>
      </q-card-section>
    </q-card>
  </q-page>
</template>

<script>
import { defineComponent } from 'vue';
import BrandsList from 'src/components/Brands/BrandsList.vue';
import { objectCopy } from 'src/assets/scripts/functions';

export default defineComponent({
  name: 'BrandsPage',

  components: { BrandsList },

  data() {
    return {
      title: 'Brands',
      searchQuery: '',
      activeFilter: '',
    };
  },

  computed: {
    filters() {
      return [
        {
          label: 'All',
          value: '',
        },
        {
          label: 'Archived',
          value: 'archived',
        },
        {
          label: 'Active',
          value: 'active',
        },
      ];
    },

    route_filter() {
      return this.$route.query.filter || '';
    },

    route_search() {
      return this.$route.query.search || '';
    },
  },

  watch: {
    searchQuery: {
      async handler(val) {
        const query = objectCopy(this.$route.query);
        query.search = val || undefined;
        this.$router.push({
          query,
        });
      },
    },

    activeFilter: {
      async handler(val) {
        const query = objectCopy(this.$route.query);
        query.filter = val || undefined;
        this.$router.push({
          query,
        });
      },
    },
  },

  async mounted() {
    this.$root.setMeta({ title: this.title });
    if (
      this.route_filter &&
      Object.values(this.filters)
        .map((e) => e.value)
        .includes(this.route_filter)
    )
      this.activeFilter = this.route_filter;
    if (this.route_search) this.searchQuery = this.route_search;
  },
});
</script>
